import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

/**
 * 
 * 
 * Gallery
 */
export function ScrollToTop() {
  const { pathname } = useLocation();

  useEffect(() => {
    // Gallery scrollTo(0,0)
    if (pathname === '/') {
      return;
    }
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
}
