// collaborations-section v1: 

import React from 'react';

import CollaborationCard from './CollaborationCard';

/**
 * 
 * @param {Array} collaborations - 
 * @param {number|null} expandedCardId - ID
 * @param {Function} onCollaborationToggle - /
 * @param {string} className - CSS
 */
const CollaborationsSection = ({
  collaborations,
  expandedCardId,
  onCollaborationToggle,
  className = '',
}) => {
  return (
    <div className={`pt-8 mt-8 bg-purple-50 rounded-lg p-6 ${className}`}>
      <h3 className='text-lg font-bold text-gray-900 mb-6'>Collaborations</h3>
      <div className='space-y-4'>
        {collaborations.map(collab => (
          <CollaborationCard
            key={collab.id}
            collaboration={collab}
            isExpanded={expandedCardId === collab.id}
            onToggle={onCollaborationToggle}
          />
        ))}
      </div>
    </div>
  );
};

export default CollaborationsSection;
